<template>
  <div id="regist" class="regist-container">
    <el-card class="box-card">
      <el-row>
        <el-col>
          <el-form status-icon>
            <el-form-item>
              <el-input type="text" prefix-icon="el-icon-mobile" v-model="account.phone" placeholder="请输入用户手机号"></el-input>
            </el-form-item>
            <el-form-item>
              <el-row>
                <el-col :span="11">
                  <el-input type="text" prefix-icon="el-icon-warning-outline" v-model="account.code" placeholder="请输入验证码"></el-input>
                </el-col>
                <el-col :span="11" style="float: right;">
                  <el-button type="primary" class="primary" round plain :disabled="isDisabled || isSendCode"
                    @click="getCode(countdown)">{{ countdown || '获取验证码' }}</el-button>
                </el-col>
              </el-row>
            </el-form-item>
            <el-form-item>
              <el-input type="password" prefix-icon="el-icon-lock" v-model="account.password" placeholder="请设置密码"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input type="password" prefix-icon="el-icon-lock" v-model="account.repassword" placeholder="请再一次设置密码"></el-input>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <el-row v-if="note">
        <el-col>
          <el-alert :title="note" type="warning" effect="light" :show-icon="true" close-text="知道了"
            @close="clearNote">
          </el-alert>
        </el-col>
      </el-row><br>
      <el-row>
        <el-col>
          <el-button size="medium" class="regist-btn" @click="regist" type="primary">注 册</el-button>
        </el-col>
      </el-row>
    </el-card>
    <regist-footer class="regist-footer"></regist-footer>
  </div>
</template>
<script>
/**
 * @author 吴永军
 * @description 注册功能
 *  */
import RegistFooter from '../components/FooterInfo'
import { promptBox } from '../service/common'
import { getValidateCode, regist } from '../service/loginServer'
export default {
  name: 'regist',
  data () {
    return {
      account: {
        phone: '',
        code: '',
        password: '',
        repassword: ''
      },
      countdown: 0,
      isDisabled: false,
      phoneReg: /^1(3|4|5|6|7|8|9)\d{9}$/,
      note: ''
    }
  },
  computed: {
    isSendCode () {
      let flag = true
      if (this.account.phone) {
        flag = false
      }
      return flag
    }
  },
  components: {
    'regist-footer': RegistFooter
  },
  created () {
    sessionStorage.clear()
  },
  methods: {
    codeCountdown () {
      let second = 60
      let inter = setInterval(() => {
        this.isDisabled = true
        this.countdown = second + '秒后重试'
        if (second === 0) {
          this.isDisabled = false
          this.countdown = '重新获取'
          clearInterval(inter)
        }
        second--
      }, 1000)
    },
    validate (target) {
      if (!this.account.phone || !this.phoneReg.test(this.account.phone)) {
        this.note = '请填写正确的手机号码'
        return false
      }
      if (target === 'regist' && !this.account.code) {
        this.note = '请输入短信验证码'
        return false
      }
      if (target === 'regist' && !this.account.password) {
        this.note = '请设置新密码'
        return false
      }
      if (target === 'regist' && this.account.password !== this.account.repassword) {
        this.note = '两次设置的密码不一致'
        return false
      }
      return true
    },
    getCode () {
      if (!this.validate('getCode')) {
        return
      }
      this.codeCountdown()
      getValidateCode(this.account.phone).then(res => {
        promptBox('友情提示', '短信验证码已发送，请注意查收。').then(info => {
          if (res.data) {
            this.account.code = res.data + ''
          }
        })
      })
    },
    regist () {
      if (!this.validate('regist')) {
        return
      }
      this.note = ''
      let params = {}
      params['account_number'] = this.account.phone
      params['password'] = this.account.password
      params['code'] = this.account.code
      regist(params).then(res => {
        let userInfo = res.data
        sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
        promptBox('友情提示', '您已注册成功，请填写入驻信息').then(info => {
          if (info === 'confirm') {
            this.$router.replace('/settleIn')
          }
        })
      })
    },
    clearNote () { this.note = '' }
  }
}
</script>
<style scoped>
.regist-container {
  width: 100%;
  height: 100%;
  background: rgba(117, 172, 230, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.box-card {
  width: 90%;
  padding: 20px 0px 20px 0px;
  z-index: 10;
}
.regist-btn {
  width: 100%;
  padding: 13px;
  font-size: 18px;
}
.regist-footer {
  position: fixed;
  bottom: 20px;
  z-index: 0;
}
</style>



